  top: -65px;
  right: -3px;
  
    transition: all 265ms ease-out;
}

.social-icons a {
    display: inline-block;
}

.social-icons a:before {
    transform: scale(1);
    -ms-transform: scale(1);
    -webkit-transform: scale(1);
    content: " ";
    width: 55px;
    height: 55px;
    border-radius: 100%;
    display: block;
    background: linear-gradient(45deg, rgb(34 0 245), rgb(143 0 125));
    transition: all 265ms ease-out;
}

.social-icons a:hover:before {
    transform: scale(0);
    transition: all 265ms ease-in;
}

.social-icons a:hover i {
    transform: scale(1.5);
    -ms-transform: scale(1.5);
    -webkit-transform: scale(1.5);
    color: #d800f5;
    background: -webkit-linear-gradient(45deg, #d39fdc, #8a008f);
