    .team-item {
      padding: 35px;
      width: 50vh;
      padding-left: 25px;
      padding-right: 0;
      position: relative;
      z-index: 0;
    }
    
    .team-item:after, .team-item:before {
      content: "";
      position: absolute;
      box-shadow: 0 5px 30px 0 rgba(39, 39, 39, 0.15);
      border-radius: 10px;
      z-index: -1;
    }
    
    .team-item:before {
      height: 100%;
      background-color: #fff;
      left: 0;
      top: 0;
      right: 30px;
      transition: all 0.3s ease;
    }
    
    .team-item:after {
      height: 6px;
      width: calc(100% - 30px);
      background-color: #4e63d7;
      left: 0;
      bottom: 0;
      transition: all 0.3s linear;
    }
    
    .team-item:hover:after {
      height: 100%;
    }
    
    .team-item .img-holder {
      flex-shrink: 0;
      box-shadow: 0 0 20px 0 rgba(51, 51, 51, 0.2);
      border-radius: 10px;
      width: calc(95% - 65px);
      margin-left: 65px;
      overflow: hidden;
    }
    
    .team-item .img-holder img {
      border-radius: 10px;
      width: 100%;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      transition: all 0.6s ease;
    }
    
    .team-item:hover .img-holder img {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    
    .team-item .socials {
      position: absolute;
    }
    
    .team-item .socials a {
      display: block;
      margin-right: 0;
      margin-bottom: 15px;
      box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.12);
    }
    
    .team-item .socials a:last-of-type {
      margin-bottom: 0;
    }
    
    .team-item .team-content {
      margin-right: 70px;
      text-align: center;
    }
    
    .team-item .team-content h5 {
      color: #101f41;
      transition: all 0.4s ease;
    }
    
    .team-item .team-content p {
      color: #4e63d7;
      transition: all 0.4s ease;
      font-weight: 600;
      font-size: 14px;
    }
    
    .team-item:hover .team-content h5, .team-item:hover .team-content p {
      color: #fff;
      transition: all 0.3s ease;
    }
    
    .d-inline-block {
        display: inline-block!important;
    }
    
    .socials a {
        width: 35px;
        height: 35px;
        background-color: #6893e1;
