@property --rotate {
    syntax: "<angle>";
    initial-value: 132deg;
    inherits: false;
  }
  .card1 { 
    background: #191c29;
    width: 400px;
    width: var(--card-width);
    min-height: var(--card-height);
    position: relative;
    border-radius: 6px;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    font-size: 1.5em;
    color: rgb(88 199 250 / 0%);
    cursor: pointer;
   margin: auto;
   margin-bottom: 15px;
  }
  
  .card1:hover {
    color: rgb(88 199 250 / 100%);
    transition: color 1s;
  }
  .card1:hover:before, .card1:hover:after {
    -webkit-animation: none;
            animation: none;
    opacity: 1;
  }
  
  
  .card1::before {
    content: "";
    width: 101%;
    height: 102%;
    border-radius: 8px;
    background-image: linear-gradient(
      var(--rotate)
      , #5ddcff, #3c67e3 43%, #4e00c2);
      position: absolute;
      z-index: -1;
      top: -1%;
      left: -0.5%;
      -webkit-animation: spin 2.5s linear infinite;
              animation: spin 2.5s linear infinite;
      opacity: 0;
  }
  .card1::after {
    position: absolute;
    content: "";
    top: calc(var(--card-height) / 6);
    left: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    -webkit-filter: blur(calc(var(--card-height) / 6));
            filter: blur(calc(var(--card-height) / 6));
    background-image: linear-gradient(
    var(--rotate)
    , #5ddcff, #3c67e3 43%, #4e00c2);
    opacity: 1;
    transition: opacity .5s;
    -webkit-animation: spin 2.5s linear infinite;
            animation: spin 2.5s linear infinite;
    opacity: 0;
  }
  @-webkit-keyframes spin {
    0% {
    --rotate: 0deg;
    }
    100% {
    --rotate: 360deg;
    }
  }
  @keyframes spin {
    0% {
    --rotate: 0deg;
    }
    100% {
    --rotate: 360deg;
    }
  }
