        text-shadow: -1px -1px 0 rgb(96, 66, 96), 1px -1px 0 rgb(144, 62, 133), -1px 1px 0 rgba(213, 45, 196, 0.952), 1px 1px 0 rgb(213, 38, 190), 0 -2px 8px, 0 0 2px, 0 0 5px #ff00f2, 0 0 15px #f44, 0 0 2px #892904, 0 2px 3px #000;
    }

    20%,
    21.999%,
    63%,
    63.999%,
    65%,
    69.999% {
        opacity: 0.4;
        text-shadow: none;
    }
}
